.app {
  & {
    max-width: 400px;
    margin: 20px auto;
    padding: 0 20px;
    box-shadow: 0 0 20px $color-black-02;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
  }
  &__body {
    display: flex;
    flex-direction: column;
  }
}
